<template>
	<section id="inventory" class="iv">
		<ul class="history__outer">
			<li v-for="items,date in purchases">
					<span class="date">{{ formatDate(date) }}</span>
					<ul class="history__inner">
						<li class="set" v-for="item in items">
							<span class="spec">{{ formatSpec(item.spec) }}</span>
							<span class="num">{{ item.num }}</span>
						</li>
					</ul>
			</li>
		</ul>
	</section>
</template>
<script>
	
	if (!Date.now) {
		Date.now = () => {
			return new Date().getTime();
		}
	}
	export default {
		name: 'Inventory',
		data() {
			return {
				purchases: ''
			}
		},
		computed: {
			
		},
		methods: {
			formatDate(date) { return date.match(/-(\S+)/)[1]; },
			formatSpec(spec) { return spec.replace(/-/, '/'); }
		},
		mounted() {
			this.db.ref('purchase').on('value', v => {
				this.purchases = v.val();
			})
		}
	}
</script>
<style lang="scss">
	@import '@/assets/inventory.scss';
</style>


